@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
* {
  box-sizing: border-box;
}

html, body {
  font-family: Helvetica;
  background: #fff;
  font-size: 14px;
  line-height: 1.0;
  height: 100%;
  width: 100%;
}

#root {
  width: 100%;
  height: 100%;
}

main {
  width: 100%;
  height: 100%;
  margin-top: 0;
}

.title-header-container {
  position: relative;
  top: 0rem;
  height: 9rem;
  margin: 0;
  z-index: 5;
  background-color: white;
  overflow: auto;
  left: 6.5%;
}

.content-container {
  position: relative;
  top: 0rem;
  height: 100%;
  margin: 0;
  z-index: 4;
  width: 85rem;
  background-color: white;
  left: 6.5%;
}

#date {
  font-size: 14px;
  padding: 0;
}

#date:active {
  border-bottom: 2px solid #21CE99;
}

#date:focus {
  border-bottom: 2px solid #21CE99;
}

#date:hover {
  border-bottom: 2px solid #21CE99;
}

.SingleDatePicker {
  padding-left: .25rem;
  margin-top: .5rem;
  margin-left: .5rem;
  border-radius: 6px;
  box-shadow: 0 1px 3px #febfb4;
  border: 2px solid #febfb4;
  display: flex;
  width: 12rem;
}

.CalendarMonth_caption strong {
  color: #21CE99;
}

.CalendarMonth_table td {
  color: #21CE99;
  background: white;
  border: 1px solid #e4e7e7;
}

.CalendarMonth_table td:hover {
  color: #febfb4;
  background: white;
  border: 1px solid #febfb4;}

.SingleDatePickerInput__withBorder {
  border: none;
}

.DateInput_input__focused {
  border-bottom: 2px solid #21CE99;
}

.main-container {
  position: relative;
  z-index: 4;
  left: 0%;
  top: 4rem;
  height: 100%;
  width: 100%;
}

h3 {
  color: #21CE99;
}

.left-right-container {
  position: relative;
  display: flex;
  top: 5rem;
  left: 4rem;
  width: 38rem;
}

.left-half {
  position: relative;
  flex: 1 1;
}

.right-half {
  flex: 1 1;
}

.slide-in-left-enter {
  transform: translateX(-100%);
}

.slide-in-left-enter-active {
  transform: translateX(0);
  opacity: 1;
  transition: all 200ms;
}

.slide-in-left-exit {
  transform: translateX(0%);
  opacity: 1;
}

.slide-in-left-exit-active {
  transform: translateX(-100%);
  opacity: 0;
  transition: all 200ms;
}

.center {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

label {
  margin-bottom: 0.5rem;
}


.center-header {
  text-align: center;
}

#username, #password {
  width: 100%;
  margin: 0;
}
.empty-list-card {
  left: 25%;
  width: 50%;
  margin-top: 1rem;
  text-align: center;
  font-size: 0;
}

.content-container .empty-list-card-center {
  left: 0;
  width: 38rem;
  margin-top: 2rem;
  text-align: center;
  font-size: 0;
  box-shadow: none;
}

.content-container .empty-list-card {
  left: 25%;
  width: 50%;
  margin-top: 4rem;
  text-align: center;
  font-size: 0;
  box-shadow: none;

}

.content-container .smaller {
  left: 25%;
  width: 50%;
  margin-top: 2rem;
  margin-bottom: 2rem;
  text-align: center;
  font-size: 0;
}

.empty-list-card h3 {
  font-size: 1.28571429rem;
}

.smaller h2 {
  font-size: 1.10rem;
}

.card {
  position: relative;
  margin: 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 3px;
  border: solid #21CE99;
  padding: 1rem;
  overflow: hidden;
  background: white;
  margin-bottom: 4rem;
}

.card:hover,
.card:active {
  border: solid #FC4D2D;
}
.form-control {
  margin: 1rem 0;
}

.form-control label,
.form-control input,
.form-control textarea {
  display: block;
}

.form-control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.form-control input,
.form-control textarea {
  width: 3rem;
  font: inherit;
  border: 1px solid #ccc;
  background: #f8f8f8;
  padding: 0.15rem 0.25rem;
}

#notes {
  width: 14rem;
  font-size: 11px;
}

.form-control textarea {
  margin-left: .5rem;
}

.form-control input:focus,
.form-control textarea:focus {
  outline: none;
  background: #ebebeb;
  border-color: #510077;
}

.form-control--invalid label,
.form-control--invalid p {
  color: red;
}

.form-control--invalid input,
.form-control--invalid textarea {
  border-color: red;
  background: #ffd1d1;
}
.button {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid #21CE99;
  border-radius: 4px;
  background: #21CE99;
  color: white;
  cursor: pointer;
  margin-right: 1rem;
  text-decoration: none;
  display: inline-block;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: #FC4D2D;
  border-color: #FC4D2D;
}

.button--inverse {
  background: transparent;
  color: #21CE99;
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background: #21CE99;
}

.button--danger {
  background: #830000;
  border-color: #830000;
}

.button--danger:hover,
.button--danger:active {
  background: #f34343;
  border-color: #f34343;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #e8faf4;
  color: #979797;
  border-color: #e8faf4;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10;
}

.modal {
  z-index: 100;
  position: fixed;
  top: 22vh;
  left: 10%;
  width: 80%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 8px;
}

.modal__header {
  width: 100%;
  padding: 1rem 0.5rem;
  background: #21CE99;
  color: white;
}

.modal__header h2 {
  margin: 0.5rem;
}

.modal__content {
  padding: 1rem 0.5rem;
}

.modal__footer {
  padding: 1rem 0.5rem;
}

@media (min-width: 768px) {
  .modal {
    left: 20%;
    width: 60%;
  }
}

.modal-enter {
  transform: translateY(-10rem);
  opacity: 0;
}

.modal-enter-active {
  transform: translateY(0);
  opacity: 1;
  transition: all 200ms;
}

.modal-exit {
  transform: translateY(0);
  opacity: 1;
}

.modal-exit-active {
  transform: translateY(-10rem);
  opacity: 0;
  transition: all 200ms;
}
.lds-dual-ring {
  display: inline-block;
  padding: 10rem;
  width: 64px;
  height: 64px;
}

.lds-dual-ring:after {
  content: ' ';
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #21CE99;
  border-color: #21CE99 transparent #21CE99 transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}

.loading-spinner__overlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.authentication__header {
  color: white;
  text-align: center;
}

.authentication {
  width: 90%;
  max-width: 25rem;
  margin: 7rem auto;
  text-align: center;
}

.authentication form {
  margin-bottom: 1rem;
}
.main-header {
  width: 100%;
  height: 4rem;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background: #21CE99;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26);
  padding: 0 1rem;
  z-index: 6;
}

@media (min-width: 768px) {
  .main-header {
    justify-content: space-between;
  }
}
.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nav-links li {
  margin: 1rem;
}

.nav-links a {
  border: 1px solid transparent;
  color: #FC4D2D;
  text-decoration: none;
  padding: 0.5rem;
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
  background: white;
  color: #21CE99;
  position: relative;
  margin: 0;
  border-radius: 6px;
  border: 1px solid #21CE99;
  padding: .5rem;
  overflow: hidden;
  background: white;
}

.nav-links button {
  cursor: pointer;
  background: white;
  padding: 0.5rem;
  font: inherit;
  border: 1px solid #FC4D2D;
  color: #FC4D2D;
  text-decoration: none;
}

.nav-links button:focus {
  outline: none;
  background: white;
  color: #21CE99;
  position: relative;
  margin: 0;
  border-radius: 6px;
  border: 1px solid #21CE99;
  padding: .5rem;
  overflow: hidden;
}

.nav-links button:hover,
.nav-links button:active {
  color: #21CE99;
  position: relative;
  margin: 0;
  border-radius: 6px;
  border: 1px solid #21CE99;
  padding: .5rem;
  overflow: hidden;
  background: white;
}

@media (min-width: 768px) {
  .nav-links {
    flex-direction: row;
  }

  .nav-links li {
    margin: 0 0.5rem;
  }

  .nav-links a {
    color: white;
    text-decoration: none;
  }

  .nav-links button {
    border: 1px solid white;
    color: white;
    background: transparent;
  }
  
  .nav-links button:hover,
  .nav-links button:active {
    background: white;
    color: #21CE99;
  }
}

.side-drawer {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  height: 100vh;
  width: 70%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
}

.main-navigation__menu-btn {
  width: 3rem;
  height: 3rem;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-right: 2rem;
  cursor: pointer;
}

.main-navigation__menu-btn span {
  display: block;
  width: 3rem;
  height: 2.5px;
  background: white;
}

.main-navigation__title {
  color: white;
}

.main-navigation__title a {
  text-decoration: none;
  color: white;
}

.main-navigation__header-nav {
  display: none;
}

.main-navigation__drawer-nav {
  height: 100%;
}

@media (min-width: 768px) {
  .main-navigation__menu-btn {
    display: none;
  }

  .main-navigation__header-nav {
    display: block;
  }
}

.main-navigation__logo {
  display: block;
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  object-fit: cover;
  margin-left: 2rem;
}
